// @ts-nocheck
import React from 'react';
import sprint from './../../assets/svg/sprint_icon.svg';
import audioCall from './../../assets/svg/audio-call_icon.svg';
import statistics from './../../assets/svg/statistics_icon.svg';
import textbook from './../../assets/svg/textbook_icon.svg';
import { Link } from 'react-router-dom';
import './advantages.css';

function Advantages() {
  const isAuthorized = localStorage.getItem('userId') ? true : false;

  return (
    <div className="advantages">
      <h1 className="advantages__title">
          <span className="marked">RSLang</span>
      </h1>
      <h4 className="advantages__subtitle">      </h4>
      <ul className="advantages__list">
        <li className="advantages__item">
          3600    ,  90%  
        </li>
        <li className="advantages__item">
              ,       
        </li>
        <li className="advantages__item">       -</li>
        <li className="advantages__item">     </li>
      </ul>
      <div className="advantages__icons-wrapper">
        <figure className="advantages__icon">
          <Link to="/textbook">
            <img className="icon_img" src={textbook} alt=""></img>
            <figcaption className="icon_desc"></figcaption>
          </Link>
        </figure>
        <figure className="advantages__icon">
          <Link to="/sprintGame">
            <img className="icon_img" src={sprint} alt=""></img>
            <figcaption className="icon_desc"></figcaption>
          </Link>
        </figure>
        <figure className="advantages__icon">
          <Link to="/audioGame">
            <img className="icon_img" src={audioCall} alt=""></img>
            <figcaption className="icon_desc"></figcaption>
          </Link>
        </figure>
        <figure className="advantages__icon">
          <Link to={isAuthorized ? '/statistics' : '/'}>
            <img className="icon_img" src={statistics} alt=""></img>
            <figcaption className="icon_desc"></figcaption>
          </Link>
        </figure>
      </div>
    </div>
  );
}

export default Advantages;
